import React from "react";
import { Link } from "react-router-dom";

export default function ButtonNext({ to, currentValue, label }) {
	const isValidated = () => {
		//inte disabled n√§r currentvalue inte √§r lika med tom str√§ng
		//allts√• knappen ska vara disabled n√§r currentvalue √§r en tom str√§ng
		return currentValue !== "";
	};
	return (
		<Link
			to={to}
			className={`btn btn-secondary btn-block ${!isValidated() && "disabled"}`}
		>
			{isValidated() ? label : "Please pick a choice!"}
		</Link>
	);
}
